import React, { useState, useEffect, useContext } from "react";
import { Auth } from "../../services/amplify";

import { useHistory, Link } from "react-router-dom";

import Menu from "../../components/Menu";

import { Card, Badge, Container, Row, Button, Spinner, Col } from "react-bootstrap";

import AuthContext from "../../contexts/auth";

import "./styles.css";

function Painel() {
  const history = useHistory();

  const [userName, setUserName] = useState("");

  const userContext = useContext(AuthContext);

  const [carregandoLogout, setCarregandoLogout] = useState(false);

  useEffect(() => {
    Auth.currentAuthenticatedUser()
      // .then((user) => console.log("Usuario Verificado: ", user))
      .catch((err) => console.log("Usuario não verificado:", err));

    Auth.currentSession()
      // .then((data) => console.log(data))
      .catch((err) => console.log(err));

    if (userContext) {
      setUserName(userContext.attributes["custom:displayname"]);
    }
  }, []);

  const signOut = () => {
    setCarregandoLogout(true);

    Auth.signOut()
      .then((data) => {
        window.location.reload();
      })
      .catch((error) => {
        setCarregandoLogout(false);
      });
  };

  return (
    <>
      <Menu logged={true} />

      {/* <Button onClick={signOut}>Sair</Button> */}

      <Container className="bg-gray container-binx" fluid>
        <Container className="panel-container">
          <Row>
            <Col>
              <div className="card-panel">
                <div className="gray-panel"></div>
                <div className="white-panel p-4">
                  <h5 className="panel-header">Vendas</h5>
                  <div className="panel-links">
                    <Link to="fretevendas" className="panel-link text-muted">
                      Cálculo de Frete
                    </Link>
                    <Link to="calculomargem" className="panel-link text-muted">
                      Cálculo de Margem
                    </Link>
                    <Link to="prospeccao" className="panel-link text-muted">
                      Prospecção de Clientes
                    </Link>
                  </div>
                </div>
              </div>
            </Col>
            <Col>
              <div className="card-panel">
                <div className="gray-panel"></div>
                <div className="white-panel p-4">
                  <h5 className="panel-header">Compras</h5>
                  <div className="panel-links">
                    <Link to="dashcompras" className="panel-link text-muted">
                      Dashboard
                    </Link>
                  </div>
                </div>
              </div>
            </Col>
          </Row>
          <Row>
            <Col>
              <div className="card-panel">
                <div className="gray-panel"></div>
                <div className="white-panel p-4">
                  <h5 className="panel-header">Expedição</h5>
                  <div className="panel-links">
                    <Link to="expedicao/aprovarpedidos" className="panel-link text-muted">
                      Aprovação de Pedidos
                    </Link>
                  </div>
                </div>
              </div>
            </Col>
            <Col>
              <div className="card-panel">
                <div className="gray-panel"></div>
                <div className="white-panel p-4">
                  <h5 className="panel-header">Relatórios</h5>
                  <div className="panel-links">
                    <Link to="relatoriominmax" className="panel-link text-muted">
                      Estoque Máximo e Mínimo
                    </Link>
                  </div>
                </div>
              </div>
            </Col>
          </Row>
        </Container>
      </Container>
    </>
  );
}

export default Painel;
